@use 'base/variables' as v;
@use 'base/mixins' as m;

.layout {
    display: flex;
    flex-direction: column-reverse;

    @include m.tablet {
        display: grid;
        grid-template-columns: 40% 60%;
        min-height: 100vh;
    }

    &__app {
        @include m.contenedor-sm;
        padding: 3rem 0;
    }

    &__imagen {
        height: 20rem;
        background-image: image-set(url('/build/img/layout.jpg') 1x, url('/build/img/layout.webp') 1x);
        background-size: cover;
        background-position: center center;

        @include m.tablet {
            height: auto;
        }
    }
}